<template>
  <el-row type="flex" justify="center">
    <el-col :span="8">
      <div>
        <el-form ref="form" :model="form" :label-position="labelPosition" :rules="rules" label-width="80px">
          <el-form-item label="新密码" prop="password">
            <el-input type="password" v-model="form.password" />
          </el-form-item>
          <el-form-item label="确认新密码" prop="passwordCheck">
            <el-input type="password" v-model="form.passwordCheck" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="button" @click="update('form')" round>确定修改</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import Qs from "qs";
export default {
  data() {
    return {
      labelPosition: "top",
      form: {
        password: "",
        passwordCheck: ""
      },
      rules: {
        password: [
          { required: true, message: "请输入新密码", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ],
        passwordCheck: [
          { required: true, message: "请确认新密码", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    update(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var data = {
            userID: sessionStorage.getItem("userID"),
            password: this.form.password
          };
          var api = "http://localhost:8888/common/password";
          this.axios
            .post(api, Qs.stringify(data))
            .then(response => {
              console.log(response);
              if (response.status == 200) {
                this.$message({
                  showClose: true,
                  message: "操作成功",
                  type: "success"
                });
                sessionStorage.clear();
                this.$router.push({ name: "login" });
              }
            })
            .catch(error => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "操作失败，请联系维护人员",
                type: "error"
              });
            });
        } else {
          this.$message({
            showClose: true,
            message: "请检查数据是否填写正确！",
            type: "error"
          });
        }
      });
    }
  }
};
</script>

